<template>
  <div class="wiki-login-main-warp">
    <div class="wiki-login-main">
      <p class="wiki-login-main-header">魔镜</p>
      <div class="wiki-login-main-content">
        <p class="login-main-content-title">登录</p>
        <div class="login-main-content-user">
          用户名：
          <el-input v-model="input" placeholder="请输入账号"></el-input>
        </div>
        <div class="login-main-content-password">
          密码：
          <el-input placeholder="请输入密码" v-model="password" show-password></el-input>
        </div>
        <div class="login-main-content-other">
            <router-link tag="a" to="/register" class="login-main-content-register">立即注册？</router-link>
            <router-link tag="a" to="/forget" target="_blank" class="login-main-content-forget">忘记密码</router-link>
        </div>
        <el-button type="primary" @click="logining">登录</el-button>
        <router-link
            to="/activation"
        >
            <el-button type="info">重新激活</el-button>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import * as ajax from "@/api";
import { mapState, mapMutations } from "vuex";

export default {
  data() {
    return {
      input: "",
      password: ""
    };
  },
  methods: {
    ...mapMutations(["loginstate_true", "setpeople"]),
    logining() {
      if (this.input == "" || this.password == "") {
        this.msgEmpty();
      } else {
        ajax.getLogin(this.input, this.password).then(resp => {
          if (resp.data.code) {
            // document.cookie = resp.data.data.access_token
            localStorage.setItem("token", resp.data.data.access_token);
            this.input = "";
            this.password = "";
            var data = {
              username: resp.data.data.user_info.username,
              email: resp.data.data.user_info.email,
              gender: resp.data.data.user_info.gender, //性别
              scores: resp.data.data.user_info.scores, //用户积分
              signature: resp.data.data.user_info.signature, //个性签名
              city: resp.data.data.user_info.city,
              avatar: resp.data.data.user_info.avatar //头像
            };
            localStorage.setItem("people", JSON.stringify(data));
            localStorage.setItem("islogin", true);
            this.loginSuccess(); //成功提示
            this.loginstate_true(); //成功登录状态
            this.setpeople(); //解决异步数据
            this.$router.push("/");
          } else {
            this.loginError();
          }
        });
      }
    },
    loginSuccess() {
      this.$message({
        message: "登录成功，欢迎来到红蓝WIKI",
        type: "success"
      });
    },
    loginError() {
      this.$message.error("用户名或密码错误");
    },
    msgEmpty() {
      this.$message.error("用户名或密码不能为空");
    }
  },
  computed: {
    ...mapState(["islogin", "people"])
  }
};
</script>

<style lang='scss'>
.wiki-login-main-warp {
  width: 100%;
  position: relative;
  .wiki-login-main {
    width: 1280px;
    // height: 100%;
    // margin: 0 auto;
    position: absolute;
    margin: auto;
    top: 138px;
    bottom: 0;
    left: 0;
    right: 0;
    &-header {
      font-size: 36px;
      text-align: center;
      margin-bottom: 20px;
    }
    &-content {
      width: 400px;
      height: 600px;
      border: 1px solid rgba(228, 228, 228, 1);
      border-radius: 5px;
      margin: 0 auto;
      background-color: #fff;
      padding: 30px;
      .login-main-content-title{
          height: 30px;
          line-height: 30px;
          font-size: 24px;
          text-align: center;
      }
      .login-main-content-user {
        margin-top: 30px;
      }
      .login-main-content-other {
          margin-top: 30px;
          font-size: 13px;
        .login-main-content-register {
          text-align: right;
          margin-top: 100px;
          color: #666;
        }
        .login-main-content-forget {
          color: red;
          float: right;
        }
        .el-input {
          margin-top: 5px;
        }
      }
      .login-main-content-password {
        margin-top: 30px;
      }
      .el-button {
        width: 340px;
        margin-top: 10px;
      }
      .el-button--info{
          margin-left: 0;
      }
    }
  }
}
</style>
